import { customElement, property } from "@simple-html/core";
import { html } from "lit-html";
import Excel from "exceljs";

@customElement("compare-excel")
export class App extends HTMLElement {
  file1Buffer: string | ArrayBuffer;
  file2Buffer: string | ArrayBuffer;
  file1: string[][] = [];
  file1Index: string[] = [];
  file2: string[][] = [];
  file2Index: any[] = [];
  errors: any[] = [];
  @property() file1Name: string = "";
  @property() file2Name: string = "";
  doubleID: boolean = false;
  openFileBtn1Bind: any;
  openFileBtn2Bind: any;
  @property() logs: string[] = [];

  updated() {
    if (!this.openFileBtn1Bind) {
      this.openFileBtn1Bind = this.openFileBtn1.bind(this);
      this.openFileBtn2Bind = this.openFileBtn2.bind(this);
      //@ts-ignore
      this.getElementsByClassName("file1")[0].addEventListener(
        "change",
        this.openFileBtn1Bind
      );
      //@ts-ignore
      this.getElementsByClassName("file2")[0].addEventListener(
        "change",
        this.openFileBtn2Bind
      );
    }
  }

  disconnectedCallback() {
    //@ts-ignore
    this.getElementsByClassName("file1")[0].removeEventListener(
      "change",
      this.openFileBtn1Bind
    );
    //@ts-ignore
    this.getElementsByClassName("file2")[0].removeEventListener(
      "change",
      this.openFileBtn2Bind
    );
  }

  openFileBtn1(e: any) {
    const file = e.target.files[0];
    this.file1Name = file.name;
    const reader = new FileReader();
    reader.readAsArrayBuffer(file);
    this.logs.unshift(`file reading:${this.file1Name}`);
    reader.onload = () => {
      this.file1Buffer = reader.result;
      this.logs.unshift(`file read:${this.file1Name}`);
      this.render();
      this.getElementsByClassName("file1")[0].setAttribute("type", "");
      this.getElementsByClassName("file1")[0].setAttribute("type", "file");
    };
  }

  openFileBtn2(e: any) {
    const file = e.target.files[0];
    this.file2Name = file.name;
    const reader = new FileReader();
    reader.readAsArrayBuffer(file);
    this.logs.unshift(`file reading:${this.file2Name}`);
    reader.onload = () => {
      this.file2Buffer = reader.result;
      this.logs.unshift(`file read:${this.file2Name}`);
      this.render();
      this.getElementsByClassName("file2")[0].setAttribute("type", "");
      this.getElementsByClassName("file2")[0].setAttribute("type", "file");
    };
  }

  async compareBtn() {
    this.file1 = [];
    this.file2 = [];
    this.file2Index = [];
    this.file2Index = [];
    this.errors = [];
    await this.readfile(
      this.file1Buffer,
      this.file1,
      this.file1Index,
      this.doubleID,
      this.file1Name
    );
    await this.readfile(
      this.file2Buffer,
      this.file2,
      this.file2Index,
      this.doubleID,
      this.file2Name
    );
    await this.compareFiles(
      this.file1Name,
      this.file2Name,
      this.doubleID,
      this.file1,
      this.file2,
      this.file2Index,
      this.file2Index,
      this.errors
    );
    this.generateErrorFile(this.errors);
  }

  hold() {
    return new Promise(resolve => {
      this.render();
      setTimeout(() => {
        resolve();
      }, 50);
    });
  }

  async compareFiles(
    file1Name: string,
    file2Name: string,
    doubleID: boolean,
    file1: string[][],
    file2: string[][],
    file1Index: string[],
    file2Index: string[],
    errors: any[]
  ) {
    const file1Column = `${file1Name}-data`;
    const file2Column = `${file2Name}-data`;

    this.logs.unshift(`Looping ${file1Name}`);
    this.logs.unshift(`Rows: ${file1Index.length}`);
    this.render();

    for (let index = 0; index < file1Index.length; index++) {
      const id = file1Index[index];
      //skip first- this is the header
      if (index % 1000 === 0) {
        //progress...
        this.logs.unshift(`Looping ${file1Name} - at index:${index}`);
        await this.hold();
      }
      if (index > 0) {
        let file2row = file2Index.indexOf(id);
        if (file2row !== -1) {
          const rowdata1 = file1[index];
          const rowdata2 = file2[file2row];
          for (let i = doubleID ? 2 : 1; i < rowdata1.length; i++) {
            // skip 2, 0 is always null and next is ID
            if (rowdata1[i] !== rowdata2[i]) {
              if (doubleID) {
                let vals = id.split(";");
                errors.push({
                  id1: vals[0],
                  id2: vals[1],
                  change: file1[0][i],
                  [`${file1Column}`]: rowdata1[i],
                  [`${file2Column}`]: rowdata2[i]
                });
              } else {
                errors.push({
                  id: id,
                  change: file1[0][i],
                  [`${file1Column}`]: rowdata1[i],
                  [`${file2Column}`]: rowdata2[i]
                });
              }
            }
          }
        } else {
          if (doubleID) {
            let vals = id.split(";");
            errors.push({
              id1: vals[0],
              id2: vals[1],
              change: `In ${file1Name} only`,
              [`${file1Column}`]: "NA",
              [`${file2Column}`]: "NA"
            });
          } else {
            errors.push({
              id: id,
              change: `In ${file1Name} only`,
              [`${file1Column}`]: "NA",
              [`${file2Column}`]: "NA"
            });
          }
        }
      }
    }

    this.logs.unshift(`Looping ${file2Name}`);
    this.logs.unshift(`Rows: ${file2Index.length}`);
    this.render();
    for (let index = 0; index < file2Index.length; index++) {
      const id = file2Index[index];
      //skip first- this is the header
      if (index % 1000 === 0) {
        this.logs.unshift(`Looping ${file2Name} - at index:${index}`);
        this.render();
        await this.hold();
      }
      if (index > 0) {
        let file1row = file1Index.indexOf(id);
        if (file1row === -1) {
          if (doubleID) {
            let vals = id.split(";");
            errors.push({
              id1: vals[0],
              id2: vals[1],
              change: `In ${file2Name} only`,
              [`${file1Column}`]: "NA",
              [`${file2Column}`]: "NA"
            });
          } else {
            errors.push({
              id: id,
              change: `In ${file2Name} only`,
              [`${file1Column}`]: "NA",
              [`${file2Column}`]: "NA"
            });
          }
        }
      }
    }
  }

  async readfile(
    buffer: any,
    intoArray: any[],
    index: string[],
    doubleID: boolean,
    filename: string
  ) {
    return new Promise(async (resolve, _reject) => {
      this.logs.unshift(`loading file into buffer <${filename}>, this can take a few seconds`);
      this.render();
      const wb = new Excel.Workbook();
      wb.xlsx.load(buffer).then(async workbook => {
        this.logs.unshift(`file loaded into buffer <${filename}>`);
        this.render();
        await this.hold();
        let worksheet: any;
        workbook.eachSheet(sheet => {
          // loop and find worksheet
          if (sheet && !worksheet) {
            worksheet = sheet;
          }
        });
        if (!worksheet) {
          this.logs.unshift("No worksheet found");
          this.render();
        } else {
          let columns = 0;
          //worksheet.eachRow(function(row: any, i: number) {
          for (let i = 1; i < worksheet._rows.length; i++) {
            const row = worksheet._rows[i];
            if (i % 1000 === 0) {
              this.logs.unshift(`reading ${filename} - at index:${i}`);
              this.render();
              await this.hold();
            }
            if (i === 1) {
              // if last column is empty later I wont know, so I need to get column count from headers
              columns = row.values.length;
            }
            let values = [];
            for (let i = 1; i < columns; i++) {
              const col = row.values[i];
              if (typeof col === "string") {
                values.push(col);
              } else {
                if (col) {
                  values.push(col.text);
                } else {
                  // empty/ null or undefined column
                  values.push("");
                }
              }
            }
            intoArray.push(values);
            if (values.length > 1) {
              if (doubleID) {
                index.push(values[0] + ";" + values[1]);
              } else {
                index.push(values[0]);
              }
            }
          } //);
        }
        resolve();
      });
    });
  }

  generateErrorFile(errors: any[]) {
    this.logs.unshift("generating errorReport");
    this.render();
    if (errors.length > 0) {
      const workbook = new Excel.Workbook();
      const worksheet = workbook.addWorksheet("errors", {
        views: [{ state: "frozen", ySplit: 1 }]
      });

      // generate columns
      const columns = [];
      for (const k in errors[0]) {
        if (errors[0] && errors[0][k] !== undefined) {
          columns.push({
            header: k,
            key: k,
            width: 10,
            style: {
              font: { name: "Calibri Light", size: 10 }
            }
          });
        }
      }
      worksheet.columns = columns;
      errors.forEach((element: any) => {
        worksheet.addRow(element);
      });

      worksheet.autoFilter = {
        from: {
          row: 1,
          column: 1
        },
        to: {
          row: errors.length,
          column: columns.length
        }
      };

      worksheet.getRow(1).font = { bold: true };

      //style it
      let rowValue = "";
      let toggle = true;
      worksheet.eachRow((row: any, _rowNumber: number) => {
        // toggle per ID (column A)
        let rowValueTemp: string = row.getCell(1) as any;
        if (rowValue !== rowValueTemp) {
          toggle = toggle ? false : true;
        }
        rowValue = rowValueTemp;

        row.eachCell(
          { includeEmpty: true },
          (cell: any, _colNumber: number) => {
            cell.border = {
              top: { style: "thin" },
              left: { style: "thin" },
              bottom: { style: "thin" },
              right: { style: "thin" }
            };

            if (toggle) {
              cell.fill = <any>{
                type: "pattern",
                pattern: "solid",
                fgColor: { argb: "FFE0E0E0" }
              };
            }
          }
        );
      });

      workbook.xlsx
        .writeBuffer({
          //@ts-ignore
          base64: true
        })
        .then(function(xls64: any) {
          // build anchor tag and attach file (works in chrome)
          var a = document.createElement("a");
          var data = new Blob([xls64], {
            type:
              "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
          });

          var url = URL.createObjectURL(data);
          a.href = url;
          a.download = "errorFile.xlsx";
          document.body.appendChild(a);
          a.click();
          setTimeout(function() {
            document.body.removeChild(a);
            window.URL.revokeObjectURL(url);
          }, 0);
        })
        .catch(function(error: any) {
          this.logs.unshift(error.message);
          this.render();
        });
    } else {
      this.logs.unshift("skipping errorReport, no errors");
      this.render();
    }
  }

  render() {
    return html`
      <div class="mt-5 ml-16 mr-16 flex flex-col items-center">
        <h1
          class="m-2 bg-gray-700 p-2 w-full font-semibold text-gray-300 text-center"
        >
          Compare excel
        </h1>
        <div>
          <input style="display: none;" class="file1" type="file" />
          <input style="display: none;" class="file2" type="file" />
          <button
            class="bg-green-700 p-2 font-semibold text-gray-300"
            @click=${() => {
              var event = new MouseEvent("click", {
                view: window,

                bubbles: false,
                cancelable: true
              });
              this.getElementsByClassName("file1")[0].dispatchEvent(event);
            }}
          >
            open file 1
          </button>
          <button
            class="bg-green-700 p-2 font-semibold text-gray-300"
            @click=${() => {
              var event = new MouseEvent("click", {
                view: window,
                bubbles: true,
                cancelable: true
              });
              this.getElementsByClassName("file2")[0].dispatchEvent(event);
            }}
          >
            open file 2
          </button>
          <button
            class="bg-green-700 p-2 font-semibold text-gray-300"
            @click=${this.compareBtn}
          >
            compare
          </button>
          <button
            class="bg-green-700 p-2 font-semibold text-gray-300"
            @click=${() => {
              this.logs = [];
            }}
          >
            clear log
          </button>
        </div>

        <h1
          class="m-2 bg-gray-700 p-2 w-full font-semibold text-gray-300 text-center"
        >
          Log
        </h1>
        <ul class="p-2 font-mono w-full bg-gray-900 text-gray-300">
          ${this.logs.map(log => {
            return html`
              <li>${log}</li>
            `;
          })}
          <ul></ul>
        </ul>
      </div>
    `;
  }
}
